:root {
  --off-white: #fafafa;
}

@font-face {
  font-family: 'MontserratAlternates';
  src: url('./Components/Fonts/MontserratAlternates-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'RobotoMedium';
  src: url('./Components/Fonts/Roboto-Medium.ttf') format('truetype');
}

.nav {
  font-family: 'MontserratAlternates', Arial, sans-serif;
  background-color: var(--off-white);
  position: sticky;
  top: 0px;
  width: 100%;
  height: 60px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 40px;
  width: 40px;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  max-width: 900px;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.nav-item {
  display: inline;
  margin-left: 2rem;
  color: #333;
}

.nav-item:hover {
  color: black;
  cursor: pointer;
}

.background-img{
  background-image: url("background-img.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  max-width: 100%;
  width: 100%;
  height: 1000px;
  font-family: 'MontserratAlternates', Arial, sans-serif;
  text-align: center;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center
}

.main-section {
  margin-top: -200px;
  display: flex;
  flex-direction: column;
  align-items: center
}

.main-title {
  color: white;
  font-size: 96px;
  margin-bottom: 0px;
}

.main-button {
  font-family: 'MontserratAlternates', Arial, sans-serif;
  border: none;
  color: white;
  padding: 10px 12px;
  text-align: center;
  font-size: 26px;
  font-weight: bold;
  border-radius: 50px;
  text-decoration: none;
  max-height: 65px;
  width: 60%;
}

.main-aref {
  margin-bottom: 25px;
}

.main-button-hacker {
  background-color: #6297C1
}

.main-button-mentor {
  background-color: #F7A3A3
}

.main-date {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.main-date-text {
  font-size: 36px;
  margin-left: 10px;
  margin-right: 10px;
}

.section {
}

.section-dark {
  background: #6F93B9;
  color: white;
}

.section-content {
  font-family: 'MontserratAlternates', Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}

.introduction {
  margin: 0 auto;
  padding: 150px;
}

.intro-content-top {
  display: flex;
  margin-bottom: 100px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: 15px;
  margin-left: 15px;
  box-sizing: border-box;
}

.rowsection3{
  display: flex;
  flex-wrap: wrap;
  margin-right: 15px;
  margin-left: 15px;
  box-sizing: border-box;
}

.col-lg {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
  position: relative;
  width: 100%;
  padding-right: 20px;
  padding-left: 15px;
  box-sizing: border-box;
}

.intro-image {
  max-width: 501px;
  max-height: 489px;
  width: auto;
  height: auto;
  display: block;
}

.intro-text {
  font-family: 'RobotoMedium', Arial, sans-serif;
  color: #114A75;
  line-height: 35px;
}

.intro-text-div {
  padding-top: 20px;
}

.intro-content-bottom {
  font-family: 'MontserratAlternates', Arial, sans-serif;
  color: #114A75;
  display: flex;
  flex-direction: column;
  text-align: center
}

.value {
  flex: 33.33%;
  padding: 25px;
}

.value-image {
  margin-bottom: 25px;
}

.list-values {
  display: flex;
  flex-direction: row;
}

.intro-content-values {
  color: #114A75;
  text-align: center;
}

.value-text {
  font-family: 'RobotoMedium', Arial, sans-serif;
  line-height: 35px;
  text-align: left;
}
